// pages/_document.tsx
import { Html, Head, Main, NextScript } from 'next/document'
import { ThemeScript } from 'next-themes/script'

export default function Document() {
  return (
    <Html lang="en">
      <Head>
        {/* Inject theme initialization early to avoid hydration mismatch */}
        <ThemeScript
          attribute="data-theme"
          defaultTheme="system"
          enableSystem={true}
          themes={['nord', 'dim']}
        />
      </Head>
      <body className="antialiased">
        <Main />
        <NextScript />
      </body>
    </Html>
  )
}